<template>
    <div class="container">
        <el-row>
          <el-col >
            <div class="divbuju">
              <el-row style="margin-bottom: 0px">
                <el-col>
                  <div class="divbuju" style="height: 50px">
                    <el-row :gutter="20">
                      <el-form :inline="true" :model="ddmForm">

                        <el-col :span="6">
                          <el-form-item>
                            
                            <el-select ref="dept" v-model="ddmForm.dept" placeholder="" @change="deptChange" clearable size="mini">
                              <el-option v-for="item in deptList" :key="item.id" :label="item.name"
                                         :value="item.deptId"></el-option>
                            </el-select>
                          </el-form-item>
                        </el-col>

                        <el-col :span="6">
                          <el-form-item>
                            
                            <el-select ref="department" v-model="ddmForm.department" placeholder="" @change="departmentChange" clearable
                                       size="mini">
                              <el-option v-for="item in departmentList" :key="item.id" :label="item.name"
                                         :value="item.departmentId"></el-option>
                            </el-select>
                          </el-form-item>
                        </el-col>

                        <el-col :span="6">
                          <el-form-item>
                            
                            <el-select ref="major" v-model="ddmForm.major" placeholder="" @change="majorChange" clearable
                                       size="mini">
                              <el-option v-for="item in majorList" :key="item.id" :label="item.name"
                                         :value="item.majorId"></el-option>
                            </el-select>
                          </el-form-item>
                        </el-col>
                        <el-col :span="6">
                          <el-form-item>
                            
                            <el-select ref="class" v-model="ddmForm.class" placeholder="" @change="getStudent" clearable size="mini">
                              <el-option v-for="item in classList" :key="item.id" :label="item.name"
                                         :value="item.classId"></el-option>
                            </el-select>
                          </el-form-item>
                        </el-col>
                      </el-form>

                    </el-row>
                  </div>
                </el-col>
              </el-row>

              <el-row style="margin-bottom: 0px">
                <el-col>
                  <div class="divbuju" style="height: 50px;padding-top: 20px">
                    <i id="etitle">{{echartstitle}}</i>
                  </div>
                </el-col>
              </el-row>

              <el-row style="margin-bottom: 0px">
                <el-col>
                  <div id="mycharts" class="divbuju" style="height: 500px">
                    
                  </div>
                </el-col>
              </el-row>


            </div>
          </el-col>
        </el-row>
    </div>
</template>

<script>
  import echarts from "echarts";
    export default {
        name: "TeaPay",
      created() {

      },
      mounted() {
        this.initDept();
        this.getDate();
      },
      methods: {
        // 
        initDept() {
          this.postRequest('/teacher/teacherclass/getDept', {}).then(resp => {
            if (resp && resp.status == 200) {
              var data = resp.data;
              this.deptList = data;
              console.info("dept--------->" + JSON.stringify(data));
            }
          });
        },

        //
        deptChange(val) {
          this.echartstitle="";
          this.deptname="";
          this.departmentname="";
          this.majorname="";
          this.classname="";

          //
          let obj = {};
          obj = this.deptList.find((item)=>{
            return item.deptId === val;
          });
          this.deptname = obj.name;


          this.departmentList = [];
          this.postRequest('/teacher/teacherclass/getDepartmentByDeptId', {
            deptId: this.ddmForm.dept
          }).then(resp => {
            if (resp && resp.status == 200) {
              var data = resp.data;
              this.departmentList = data;
              this.getDate();
            }
          });
        },

        //
        departmentChange(val) {
          this.departmentname="";
          this.majorname="";
          this.classname="";

          //
          let obj = {};
          obj = this.departmentList.find((item)=>{
            return item.departmentId === val;
          });
          this.departmentname = obj.name;

          this.majorList = [],
            this.postRequest('/teacher/teacherclass/getMajorByDepartmentId', {
              departmentId: this.ddmForm.department
            }).then(resp => {
              if (resp && resp.status == 200) {
                var data = resp.data;
                this.majorList = data;
                this.getDate();
              }
            });
        },

        //
        majorChange(val) {
          this.majorname="";
          this.classname="";
          //
          let obj = {};
          obj = this.majorList.find((item)=>{
            return item.majorId === val;
          });
          this.majorname = obj.name;


          this.classList = [],
            this.postRequest('/teacher/teacherclass/getClassByMajorId', {
              majorId: this.ddmForm.major
            }).then(resp => {
              if (resp && resp.status == 200) {
                var data = resp.data;
                this.classList = data;
                this.getDate();
                console.info("" + JSON.stringify(this.classList))
              }
            });
        },


        //&&
        getStudent(val) {

          this.classname="";
          //
          let obj = {};
          obj = this.classList.find((item)=>{
            return item.classId === val;
          });
          this.classname = obj.name;
          this.getDate();
        },

        //
        ringConfigure() {
          // console.log(this.userJson)document.getElementById('myEchart')this.$refs.myEchart
          let myChart = echarts.init(document.getElementById('mycharts')); //
          window.onresize = myChart.resize;
          var option = {
            tooltip: {
              trigger: 'item',
              formatter: "{a} <br/>{b}: {c} ({d}%)"
            },
            legend: {
              orient: 'vertical',
              x: 'left',
              data:['','','']
            },
            series: [
              {
                name:'',
                type:'pie',
                radius: ['50%', '70%'],
                avoidLabelOverlap: false,
                label: {
                  normal: {
                    show: false,
                    position: 'center'
                  },
                  emphasis: {
                    show: true,
                    textStyle: {
                      fontSize: '30',
                      fontWeight: 'bold'
                    }
                  }
                },
                labelLine: {
                  normal: {
                    show: false
                  }
                },
                data:this.payinfo
              }
            ]
          };

          myChart.setOption(option);
        },

        //
        getDate(){
          let word="";
          if(this.deptname!=""){

              word+=this.deptname;
          }
          if(this.departmentname!=""){
            word+=this.departmentname;
          }
          if(this.majorname!=""){
            word+=this.majorname;
          }
          if(this.classname!=""){
            word+=this.classname;
          }
          if(word!=""){
            this.echartstitle=word;
          }
          this.postRequest('/teacher/teacherpay/getPayInfo', {
            deptId: this.ddmForm.dept,
            departmentId: this.ddmForm.department,
            majorId: this.ddmForm.major,
            classId: this.ddmForm.class
          }).then(resp => {
            if (resp && resp.status == 200) {
              var data = resp.data;
              this.payinfo = data;
              this.ringConfigure();
            }
          });
        },

      },
      data(){
        return {
          //
          echartstitle:"",
          ddmForm: {

            class: "",
            major: "",
            department: "",
            dept: "",

          },
          //
          classList: [],
          classname:"",

          //
          majorList: [],
          majorname:"",
          //
          departmentList: [],
          departmentname:"",
          //
          deptList: [],
          deptname:[],

          //
          payinfo:[],
        }
      }
    }
</script>

<style scoped>
#etitle{
  /*font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","",Arial,sans-serif;*/
  color: #37a0dd;
}
</style>



