<template>
    <section class="container">
        <el-row>
            <el-col :span="6">
                <div class="divbuju" style="height: 400px ;overflow-y:auto;">
                    <el-row>
                        <el-col>
                          <el-select v-model="applystate" placeholder="" @change="getApplyfilestate" clearable>
                            <el-option v-for="item in stateOptions"  :key="item.value" :label="item.label"
                                       :value="item.value"></el-option>
                          </el-select>
                        </el-col>
                    </el-row>
                  <el-row>
                    <el-col :span="8">
                        
                    </el-col>
                  </el-row>

                  <div class="divbuju" v-infinite-scroll="load">
                    <div v-for="a in applys" style="margin-bottom: 10px">
                      <el-button type="info" style="width: 100%"  @click="getapplyfile(a)">{{a.ss.sb.name}}</el-button>
                    </div>

                  </div>
                </div>
            </el-col>
            <el-col :span="18">
              <div class="divbuju" style="height: 400px ;overflow-y:auto;">

                <div v-for="af in applyfiles" style="border-bottom:1px solid #000">
                <el-row>
                    <el-col :span="16">{{af.filename}}</el-col>
                    <el-col :span="8"><el-button type="primary" @click="downloadfile(af.fileurl)" round></el-button></el-col>
                </el-row>
                  <el-row>
                    <el-col :span="16">{{af.remarks}}</el-col>
                    <el-col :span="8" v-if="ap.state==''"><el-button type="primary" @click="addremarksFrom(af)" round></el-button></el-col>
                  </el-row>
                </div>

                <el-row :gutter="50" v-if="ap.state==''">
                  <el-col :span="6" :offset="16"><el-button type="success" @click="adoptapply"></el-button></el-col>
                </el-row>

              </div>
            </el-col>
        </el-row>

      <!--  -->
      <el-dialog title="" :visible.sync="remarksFromVisible" :close-on-click-modal="false">
        <el-form :model="applyfile" label-width="80px" ref="sgincn" :inline="true">
          <el-form-item label="" prop="noticeWord">
            <el-input type="textarea" style="width: 600px" :rows="3" placeholder="" v-model="applyfile.remarks" auto-complete="off" clearable></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="remarksFromVisible = false"></el-button>
          <el-button @click="submitremarks"></el-button>
        </div>
      </el-dialog>



    </section>
</template>

<script>
    export default {
        name: "TeaApply",
      created() {

      },
      mounted() {
          this.applystate="";
          this.getApplyfilestate();
      },
      methods: {
          //
        getApplyfilestate(state){
          this.ap={},
          this.getapplys();
        },

        //
        getapplys(){
          this.applyfiles=[];
          this.postRequest('/teacher/teacherapply/getApply',{
            state:this.applystate
          }).then(resp => {
            this.applys=resp.data;
            console.info("" + JSON.stringify(resp.data))
          })
        },
        //
        getapplyfile(a){
          this.ap=a;
          this.postRequest('/teacher/teacherapply/getApplyfile',{
            studentId:this.ap.ss.studentId
          }).then(resp => {
            this.applyfiles=resp.data;
            console.info("" + JSON.stringify(resp.data))
          })

        },

//
        downloadfile(fileurl){
          window.location="/student/studentpay/download?fileurl="+fileurl;
        },

      //
        addremarksFrom(af){
          if(af.state==""){
            this.$message({
              message:"",
              type:"success"
            });
          }else{
            this.applyfile.id=af.id;
            this.applyfile.remarks=af.remarks;
            this.remarksFromVisible=true;
          }
        },

        //
        submitremarks(){
          if(this.applyfile.remarks!=""){
            this.postRequest('/teacher/teacherapply/addremarks',{
              id:this.applyfile.id,
              remarks:this.applyfile.remarks
            }).then(resp => {
              this.getapplyfile(this.ap);
              this.remarksFromVisible=false;
            })
          }else{
            this.$message({
              message:"",
              type:"warning"
            });
          }
        },
        //
        adoptapply(){
          let newapply=this.ap;
          this.$confirm(""+newapply.ss.sb.name+"?", "", {
            type: "warning"
          }).then(() => {
            this.postRequest('/teacher/teacherapply/applyok',{
              id:newapply.id,
              studentId:newapply.studentId
            }).then(resp => {
              if(resp.status==200){
                this.getapplys();
              }
            })
          })
            .catch(() => {
            });
        },
      },
      data(){
        return {
          applystate:"",

          stateOptions: [
            {
              value: "",
              label: ""
            },
            {
              value: "",
              label: ""
            }
          ],

          //
          applys:[],

          //
          ap:{},

          //
          applyfiles:[],


          applyfile:{
            id:"",
            remarks:"",
          },

          //
          remarksFromVisible:false,

        }
      }
    }
</script>

<style scoped>

</style>



