<template xmlns:el-col="http://www.w3.org/1999/html">
  <section class="container">
    <div>

      <el-tabs :tab-position="tabPosition" style="height: 200px;" @tab-click="clicktab">


        <el-tab-pane  label="" >

          <div class="divbuju" style="width: 100%; height: 600px;">

            <el-row>
              <el-col :span="4"></el-col>
              <el-col :span="4"></el-col>
              <el-col :span="4"></el-col>
              <el-col :span="4"></el-col>
            </el-row>

            <div class="divbuju" style="height: 50px" v-for="mas in majorandstudentlist">
              <el-row>
                  <el-col :span="4">{{mas.major}}</el-col>
                  <el-col :span="4">{{mas.boynumber}}</el-col>
                  <el-col :span="4">{{mas.girlnumber}}</el-col>
                  <el-col :span="4">

                    <el-input v-model.number="mas.classnumber" placeholder=""
                      auto-complete="off" @blur="changenumber(mas)"></el-input>

                  </el-col>

                <el-col :span="8" v-if="mas.isshow">{{mas.word}}</el-col>

              </el-row>
            </div>

                                  <el-row>
                                    <div style="margin-top: 20px">
                                      <el-col :span="4" :offset="20">
                                        <div class="grid-content bg-purple">
                                          <el-button type="success" icon="el-icon-circle-plus-outline" @click="assignmentClass" size="mini" round>
                                            
                                          </el-button>
                                        </div>
                                      </el-col>
                                    </div>
                                  </el-row>
                                </div>


                      <!--          <template>-->
<!--            <el-table-->
<!--              :data="majorandstudentlist"-->
<!--              style="width: 100%"-->
<!--              :v-loading="majornameLoading"-->
<!--            >-->
<!--              <el-table-column-->
<!--                prop="major"-->
<!--                label="">-->
<!--              </el-table-column>-->
<!--              <el-table-column-->
<!--                prop="boynumber"-->
<!--                label="">-->
<!--              </el-table-column>-->
<!--              <el-table-column-->
<!--                prop="girlnumber"-->
<!--                label="">-->
<!--              </el-table-column>-->

<!--              <el-table-column-->
<!--                label="">-->
<!--                <template slot-scope="scope">-->
<!--                  <el-input v-model.number="scope.row.classnumber" placeholder=""-->
<!--                            auto-complete="off" @blur="changenumber(scope.$index,scope.row)"></el-input>-->
<!--                </template>-->
<!--              </el-table-column>-->

<!--              <el-table-column-->
<!--                label="">-->
<!--                <template slot-scope="scope">-->
<!--                  <div v-if="true"> {{scope.row.word}}</div>-->
<!--                </template>-->
<!--              </el-table-column>-->
<!--            </el-table>-->
<!--          </template>-->



        </el-tab-pane>


        <el-tab-pane  label="" >
          <el-row>

            <el-col :span="6">
              <div class="divbuju">
                <el-row>
                  <el-col :span="12">
                    <h2>:</h2>
                  </el-col>
                  <el-col :span="12">
                    <el-button type="warning" style="margin-top: 20px" class="el-icon-refresh-right"@click="shuaxin()" circle></el-button>
                  </el-col>
                </el-row>
              </div>

              <div class="divbuju" style="height: 500px;overflow-y: auto">
              <el-tree :data="treeData" :props="defaultProps" accordion node-key="id" style="margin-left: 20%"
                       :expand-on-click-node="false">
                        <span class="custom-tree-node" slot-scope="{ node, data }">
                            <span>
                           <i  @click="changtable(node)">{{node.label}}</i>
                           <i title="" v-if="!node.label.endsWith('')" class="el-icon-plus"
                              @click="() => addNewClass(node)"></i>
                           <i v-if="(node.label.endsWith(''))" class="el-icon-delete"
                              @click="() => removeClass(node, data)"></i>
                           </span>
                        </span>
              </el-tree>
              </div>
            </el-col>

            <el-col :span="18">
              <div class="divbuju" style="height: 600px;overflow-y: auto">
              <div class="handle-box"> <!-- begin -->
                <!---->
                <el-row :gutter="20">
                  <el-form :inline="true" :model="ddmForm">

                    <el-col :span="6">
                      <el-form-item>
                        
                        <el-select v-model="ddmForm.dept" placeholder="" @change="deptChange" clearable size="mini">
                          <el-option v-for="item in deptList" :key="item.id" :label="item.name"
                                     :value="item.deptId"></el-option>
                        </el-select>
                      </el-form-item>
                    </el-col>

                    <el-col :span="6">
                      <el-form-item>
                        
                        <el-select v-model="ddmForm.department" placeholder="" @change="departmentChange" clearable
                                   size="mini">
                          <el-option v-for="item in departmentList" :key="item.id" :label="item.name"
                                     :value="item.departmentId"></el-option>
                        </el-select>
                      </el-form-item>
                    </el-col>

                    <el-col :span="6">
                      <el-form-item>
                        
                        <el-select v-model="ddmForm.major" placeholder="" @change="majorChange" clearable
                                   size="mini">
                          <el-option v-for="item in majorList" :key="item.id" :label="item.name"
                                     :value="item.majorId"></el-option>
                        </el-select>
                      </el-form-item>
                    </el-col>
                    <el-col :span="6">
                      <el-form-item>
                        
                        <el-select v-model="ddmForm.class" placeholder="" @change="getStudent" clearable size="mini">
                          <el-option v-for="item in classList" :key="item.id" :label="item.name"
                                     :value="item.classId"></el-option>
                        </el-select>
                      </el-form-item>
                    </el-col>
                  </el-form>

                </el-row>
              </div> <!-- end -->

              <el-row>
                <el-col :span="4">
                  <div class="grid-content bg-purple">

                    <el-button type="danger" icon="el-icon-delete" @click="handleDeleteList" size="mini" round>
                    </el-button>
                  </div>
                </el-col>
                <el-col :span="4">
                  <div class="grid-content bg-purple-light">
                    <el-button type="success" icon="el-icon-circle-plus-outline" @click="handleAddList" size="mini" round>
                    </el-button>
                  </div>
                </el-col>
                <el-col :span="4">
                  <div class="grid-content bg-purple-light">
                    <el-button type="warning" icon="el-icon-warning" @click="selectnoclassstudent" size="mini" round>
                    </el-button>
                  </div>
                </el-col>
              </el-row>

              <template>
                <!---->
                <el-table :data="studentList.slice((currentPage-1)*pagesize,currentPage*pagesize)"
                          size="mini"
                          highlight-current-row border
                          class="el-tb-edit mgt20"
                          ref="multipleTable"
                          tooltip-effect="dark"
                          v-loading="studentLoading"
                          @selection-change="selectChange">
                  <!---->
                  <el-table-column type="selection" width="55">
                  </el-table-column>
                  <!---->
                  <el-table-column type="index" :index="indexMethod">
                  </el-table-column>
                  <el-table-column label="" prop="sb.name">
                  </el-table-column>
                  <el-table-column label="" prop="sb.sex">
                  </el-table-column>
                  <el-table-column label="" prop="sb.phone">
                  </el-table-column>
                  <el-table-column label="" prop="sb.email">
                  </el-table-column>

                </el-table>
              </template>
              <br>
              <br>
              <!--  -->
              <el-pagination @size-change="handleSizeChange"
                             @current-change="handleCurrentChange"
                             :current-page="currentPage"
                             :page-sizes="[5, 10, 20, 40]"
                             :page-size="pagesize"
                             layout="total, sizes, prev, pager, next, jumper"
                             :total="studentList.length">
              </el-pagination>

              </div>
            </el-col>


          </el-row>


        </el-tab-pane>

      </el-tabs>

      <!--  -->
      <el-dialog title="" :visible.sync="addStudentFromVisible" :close-on-click-modal="false" v-loading="addStudentFromLoading">
        <div>
          {{major}}
          <div>
            <template>
              <!---->
              <el-table :data="addStudentList.slice((currentPage-1)*pagesize,currentPage*pagesize)"
                        size="mini"
                        highlight-current-row border
                        class="el-tb-edit mgt20"
                        ref="multipleTable"
                        tooltip-effect="dark"
                        v-loading="addStudentFromLoading"
                        @selection-change="selectChange">
                <!---->
                <el-table-column type="selection" width="55">
                </el-table-column>
                <!---->
                <el-table-column type="index" :index="indexMethod">
                </el-table-column>
                <el-table-column label="" prop="sb.name">
                </el-table-column>
                <el-table-column label="" prop="studentId">
                </el-table-column>
                <el-table-column label="" prop="sb.sex">
                </el-table-column>
                <el-table-column label="" prop="sb.phone">
                </el-table-column>
                <el-table-column label="" prop="sb.email">
                </el-table-column>

              </el-table>
            </template>
            <br>
            <br>
            <!--  -->
            <el-pagination @size-change="handleSizeChange"
                           @current-change="handleCurrentChange"
                           :current-page="currentPage"
                           :page-sizes="[5, 10, 20, 40]"
                           :page-size="pagesize"
                           layout="total, sizes, prev, pager, next, jumper"
                           :total="addStudentList.length">
            </el-pagination>

          </div>
        </div>
        <div slot="footer" class="dialog-footer">
          <el-button @click="addStudentFromVisible=false"></el-button>
          <el-button @click="addStudentToCLass"></el-button>
        </div>
      </el-dialog>


    </div>
  </section>

</template>

<script>
  export default {
    name: "TeaClass",
    created() {
      this.initmajorandstudent();
      this.initTreeData();
    },
    mounted() {
     this.selectnoclassstudent();
    },
    methods: {
      initmajorandstudent() {
        this.majornameLoading = true;
        //
        this.postRequest('/teacher/teacherclass/getallmajorbynow', {}).then(resp => {
          if (resp && resp.status == 200) {
            var data = resp.data;
            console.info("major--------->" + JSON.stringify(data));
            this.majorandstudentlist = data;
            this.majornameLoading = false;
          }
        });
      },
      changenumber(row) {
        if (row.classnumber > 0) {
          let allnumber = row.boynumber + row.girlnumber;
          let personnumber = allnumber / row.classnumber;
          if(personnumber<25){
            this.$message({
              message: "25",
              type: "error"
            });
            row.classnumber = 0;
            row.isshow=false;

            return;
          }
          let word = row.major + "" + allnumber + "," + row.classnumber +
            "" + Math.round(personnumber) + "";
          row.isshow=true;
          row.word=word;

        } else {
          this.$message({
            message: "0",
            type: "error"
          });
          row.classnumber = 0;
          row.isshow=false;
        }
      },
      //
      assignmentClass() {
        console.info("" + JSON.stringify(this.numberlist));
        if (this.numberlist != "") {
          this.numberlist = "";
        }
        let maslist = this.majorandstudentlist;
        let errorword = "";
        for (let i = 0; i < maslist.length; i++) {
          if (maslist[i].classnumber <= 0) {
            errorword += maslist[i].major + "";
          }
          this.numberlist += maslist[i].major + ":" + maslist[i].classnumber + ",";
          console.info("" + JSON.stringify(this.numberlist));
        }
        this.numberlist = this.numberlist.substring(0, this.numberlist.length - 1);
        console.info("" + JSON.stringify(this.numberlist));

        if (errorword != "") {
          this.$message(
            {
              message: errorword + "",
              type: "error"
            }
          );
          this.numberlist = "";
          console.info("" + JSON.stringify(this.numberlist));
        } else {
          if (this.numberlist == "") {
            this.$message(
              {
                message: "",
                type: "error"
              }
            );
          } else {
            this.$confirm("", "", {
              type: "warning"
            }).then(() => {
              this.majornameLoading = true;
              let finanumber = this.numberlist;
              console.info(finanumber);
              this.postRequest('/teacher/teacherclass/initclass', {
                finanumber: finanumber
              }).then(resp => {
                if (resp && resp.status == 200) {
                  this.numberlist = "";
                  this.majornameLoading = false;
                  this.initmajorandstudent();
                }
              });
            }).catch(() => {
              this.numberlist = "";
              this.majornameLoading = false;
            });
          }
        }
      },


      // 
      initDept() {
        this.postRequest('/teacher/teacherclass/getDept', {}).then(resp => {
          if (resp && resp.status == 200) {
            var data = resp.data;
            this.deptList = data;
            console.info("dept--------->" + JSON.stringify(data));
          }
        });
      },

      //
      deptChange(id) {
        this.departmentList = [];
        this.postRequest('/teacher/teacherclass/getDepartmentByDeptId', {
          deptId: this.ddmForm.dept
        }).then(resp => {
          if (resp && resp.status == 200) {
            var data = resp.data;
            this.departmentList = data;
          }
        });
      },

      //
      departmentChange(id) {
        this.majorList = [],
          this.postRequest('/teacher/teacherclass/getMajorByDepartmentId', {
            departmentId: this.ddmForm.department
          }).then(resp => {
            if (resp && resp.status == 200) {
              var data = resp.data;
              this.majorList = data;
            }
          });
      },

      //
      majorChange(id) {
        this.classList = [],
          this.postRequest('/teacher/teacherclass/getClassByMajorId', {
            majorId: this.ddmForm.major
          }).then(resp => {
            if (resp && resp.status == 200) {
              var data = resp.data;
              this.classList = data;
              console.info("" + JSON.stringify(this.classList))
            }
          });
      },


      //&&
      getStudent() {
        console.info("ddmFrom--------------->" + JSON.stringify(this.ddmForm))
        this.studentLoading = true;
        this.signclassId=this.ddmForm.class;
        this.postRequest('/teacher/teacherclass/getStudentStatus', {
          deptId: this.ddmForm.dept,
          departmentId: this.ddmForm.department,
          majorId: this.ddmForm.major,
          classId: this.ddmForm.class
        }).then(resp => {
          if (resp && resp.status == 200) {
            var data = resp.data;
            this.studentList = data;
            this.studentLoading = false;
          }
        });
      },
      // currentPagepagesizedata
      handleSizeChange: function (size) {
        this.pagesize = size;
        console.log(this.pagesize)  //
      },
      handleCurrentChange: function (currentPage) {
        this.currentPage = currentPage;
        console.log(this.currentPage)  //
      },
      indexMethod(index) {
        return index + 1;
      },

      //
      selectChange: function (val) {
        this.selectList = val;
      },

      //
      initTreeData() {
        this.postRequest('/teacher/teacherclass/initTreeData', {}).then(resp => {
          if (resp && resp.status == 200) {
            var data = resp.data;
            this.treeData = data;
            //
            this.initDept();
            console.info("" + JSON.stringify(this.treeData))
          }
        });
      },
      //
      handleDeleteList: function () {
        const length = this.selectList.length;
        let id = "";
        for (let i = 0; i < length; i++) {
          id += this.selectList[i].id + ",";
        }
        //,
        id = id.substring(0, id.length - 1);
        console.info(id);
        var word = "" + id + "" + this.studentList[0].classId + "";
        this.$confirm(word, "", {
          type: "warning"
        }).then(() => {
          this.postRequest('/teacher/teacherclass/deletelistclassformstudent', {
            id: id
          }).then(resp => {
            if (resp && resp.status == 200) {
              this.selectList = [];
              this.getStudent();
            }
          });
        })
          .catch(() => {
          });
      },


      //
      selectnoclassstudent() {
        this.getRequest('/teacher/teacherclass/getMajorAndStudentSun').then(resp => {
          if (resp && resp.status == 200) {
            var data = resp.data;
            var word = "";
            for (var i = 0; i < data.length; i++) {
              word += data[i].major + "" + data[i].classnumber + ""
            }
            if (word != "") {
              this.$notify({
                title: '',
                message: ''+word,
                type: 'warning',
                duration: 0
              });
            }
          }
        });
      },
      //
      removeClass(node, data) {
        console.info(JSON.stringify(node.data));
        this.$confirm("" + node.data.name + "", "", {
          type: "warning"
        }).then(() => {
          this.postRequest('/teacher/teacherclass/deleteclassandformstudent', {
            id: node.data.classId
          }).then(resp => {
            if (resp && resp.status == 200) {
              this.selectList = [];
              this.initTreeData();
            }
          });
        })
          .catch(() => {
          });
      },

      //
      addNewClass(node) {
        console.info(JSON.stringify(node.data));
        var children=node.data.children;
        let maxclassId=0;
        for(let i=0;i<children.length;i++){
          let tempclassId=children[i].classId;
          console.info(i+":"+tempclassId)
          if(maxclassId<tempclassId){
            maxclassId=tempclassId;
            console.info(i+":"+maxclassId)
          }
        }
        let oldlen=maxclassId.length;

        maxclassId=parseInt(maxclassId)+1;


        console.info("+1:"+maxclassId);
        let smaxclassId=String(maxclassId);
        let newlen=smaxclassId.length;
        let name =parseInt(smaxclassId.slice(smaxclassId.length-2))+"";

        console.info(""+oldlen+newlen+(oldlen>newlen));
        if(oldlen>newlen){
          smaxclassId="0"+smaxclassId;
        }
        console.info(""+name+smaxclassId);
        this.$confirm("" + node.label + "", "", {
          type: "warning"
        }).then(() => {
          this.postRequest('/teacher/teacherclass/getNewClass', {
            classId: smaxclassId,
            name:name,
            grade:children[0].grade,
            majorId:children[0].majorId
          }).then(resp => {
            if (resp && resp.status == 200) {
              this.initTreeData();
            }
          });
        })
          .catch(() => {
          });

      },

      //
      handleAddList() {
        if(this.signclassId!=""){
          this.addStudentFromVisible=true;
          this.addStudentFromLoading=true;
          this.postRequest('/teacher/teacherclass/getNoCLassStudentSunByMajorId', {
            classId: this.signclassId
          }).then(resp => {
            if (resp && resp.status == 200) {
              console.info(JSON.stringify(resp.data.allsss))
              this.addStudentList=resp.data.allsss;
              this.major=resp.data.major;
              this.addStudentFromLoading=false;
            }
          });
        }else{
          this.$message({
            message:'',
            type:'error'
          });
        }
      },

      //
      addStudentToCLass(){
        const length = this.selectList.length;
        let id = "";
        for (let i = 0; i < length; i++) {
          id += this.selectList[i].id + ",";
        }
        //,
        id = id.substring(0, id.length - 1);
        if(id!=""){
          console.info(id);
          var word = "" + id + "" + this.signclassId + "";
          this.$confirm(word, "", {
            type: "warning"
          }).then(() => {
            this.postRequest('/teacher/teacherclass/addStudentCLassByid', {
              id: id,
              classId:this.signclassId
            }).then(resp => {
              if (resp && resp.status == 200) {
                this.selectList = [];
                this.ddmForm.class=this.signclassId;
                this.getStudent();
                this.addStudentFromVisible=false;
              }
            });
          })
            .catch(() => {
            });
        }

      },

      //
      changtable(node) {
        if (node.label.endsWith('')) {
          this.signclassId=node.data.classId;
          this.studentLoading = true;
          this.postRequest('/teacher/teacherclass/getStudentStatus', {
            classId: node.data.classId
          }).then(resp => {
            if (resp && resp.status == 200) {
              var data = resp.data;
              this.studentList = data;
              this.studentLoading = false;
            }
          });
        }
      },
      //
      shuaxin(){
        this.initTreeData();
      },

      //tab
      clicktab(tab){
        if(tab.label==''){
          this.initTreeData();
        }else{
          this.initmajorandstudent();
        }
      }

    },
    data() {
      return {
        majornameLoading: false,
        majorandstudent: {
          major: "",
          boynumber: "",
          girlnumber: "",
          classnumber: "0",
        },
        majorandstudentlist: [],
        numberlist: "",
        submitLoading: false,

        tabPosition: "top",

        changid: "",


        //
        classList: [],

        //
        majorList: [],
        //
        departmentList: [],
        //
        deptList: [],

        ddmForm: {

          class: "",
          major: "",
          department: "",
          dept: "",

        },

        // 

        studentList: [],
        studentLoading: false,

        currentPage: 1, //
        pagesize: 5,    //    

        selectList: [],


        defaultProps: {
          children: 'children',
          label: 'name'
        },

        treeData: [],

        //id
        signclassId:"",
        major:"",
        addStudentFromLoading:false,
        addStudentFromVisible:false,
        addStudentList:[],
      }
    },
  }
</script>

<style scoped>
  .container {
    height: 700px;
    padding: 30px;
    background: #fff;
    border: 1px solid #ddd;
    border-radius: 5px;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .12), 0 0 6px 0 rgba(0, 0, 0, .04);
  }
</style>



