<template>
  <section>
    <div class="table"> <!-- begin -->
      <div class="container"><!-- begin -->
        <div class="handle-box"> <!-- begin -->
          <!---->
          <el-row :gutter="20">
            <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
              <el-form :inline="true" :model="classForm">
                <el-form-item>
                  
                  <el-select v-model="classForm.director" placeholder="" clearable size="mini">
                    <el-option v-for="item in directorList" :key="item.teacherId" :label="item.name"
                               :value="item.teacherId"></el-option>
                  </el-select>
                </el-form-item>

                <el-form-item>
                  
                  <el-select v-model="classForm.major" placeholder="" clearable size="mini">
                    <el-option v-for="item in majorList" :key="item.name" :label="item.name"
                               :value="item.name"></el-option>
                  </el-select>
                </el-form-item>

                <el-form-item>
                  
                  <el-select v-model="classForm.department" placeholder="" clearable size="mini">
                    <el-option v-for="item in departmentList" :key="item.name" :label="item.name"
                               :value="item.name"></el-option>
                  </el-select>
                </el-form-item>

                <el-form-item>
                  
                  <el-select v-model="classForm.dept" placeholder="" clearable size="mini">
                    <el-option v-for="item in deptList" :key="item.name" :label="item.name"
                               :value="item.name"></el-option>
                  </el-select>
                </el-form-item>

                <el-form-item>
                  <el-button type="primary" icon="el-icon-search" @click="getAllClass()" size="mini"></el-button>
                </el-form-item>

              </el-form>
            </el-col>

          </el-row>
        </div> <!-- end -->

        <template>
          <!---->
          <el-table :data="classList.slice((currentPage-1)*pagesize,currentPage*pagesize)"
                    size="mini"
                    highlight-current-row border
                    class="el-tb-edit mgt20"
                    ref="multipleTable"
                    tooltip-effect="dark"
                    v-loading="initLoading"
                    @selection-change="selectChange">
            <!---->
            <el-table-column type="selection" width="55">
            </el-table-column>
            <!---->
            <el-table-column type="index" :index="indexMethod">
            </el-table-column>
            <el-table-column label="" prop="dept">
            </el-table-column>
            <el-table-column label="" prop="department">
            </el-table-column>
            <el-table-column label="" prop="major">
            </el-table-column>
            <el-table-column label="" prop="grade">
            </el-table-column>
            <el-table-column label="" prop="name">
            </el-table-column>
            <el-table-column label="" prop="classId">
              <template slot-scope="scope">  <!-- -->
                <el-button type="primary" icon="el-icon-search" circle size="small" @click="showStudentInfo(scope.row)">
                  
                </el-button>
              </template>
            </el-table-column>
            <el-table-column label="" prop="director">
              <template slot-scope="scope">
                <el-button @click="showdirector(scope.row)" type="text" size="small">{{scope.row.director}}</el-button>
              </template>
            </el-table-column>

            <el-table-column fixed="right" label="" width="150">
              <template slot-scope="scope">  <!-- @click="handleEdit(scope.$index,scope.row)"-->
                <el-select v-model="scope.row.director" @change="directorchage(scope.row)" placeholder="" clearable
                           size="mini">
                  <el-option v-for="item in directorList" :key="item.teacherId" :label="item.name"
                             :value="item.teacherId"></el-option>
                </el-select>
              </template>
            </el-table-column>
          </el-table>
        </template>
        <br>
        <br>
        <!--  -->
        <el-pagination @size-change="handleSizeChange"
                       @current-change="handleCurrentChange"
                       :current-page="currentPage"
                       :page-sizes="[5, 10, 20, 40]"
                       :page-size="pagesize"
                       layout="total, sizes, prev, pager, next, jumper"
                       :total="classList.length">
        </el-pagination>
      </div><!-- end-->

    </div><!-- end -->


    <!--  -->
    <el-dialog title="" :visible.sync="studentFormVisible" :close-on-click-modal="false" v-loading="studentLoading">
            <div>
              <div>
                <template>
                  <!---->
                  <el-table :data="studentList.slice((currentPage-1)*pagesize,currentPage*pagesize)"
                            size="mini"
                            highlight-current-row border
                            class="el-tb-edit mgt20"
                            ref="multipleTable"
                            tooltip-effect="dark"
                            v-loading="studentLoading"
                            @selection-change="selectChange">
                    <!---->
                    <el-table-column type="selection" width="55">
                    </el-table-column>
                    <!---->
                    <el-table-column type="index" :index="indexMethod">
                    </el-table-column>
                    <el-table-column label="" prop="sb.name">
                    </el-table-column>
                    <el-table-column label="" prop="sb.sex">
                    </el-table-column>
                    <el-table-column label="" prop="sb.phone">
                    </el-table-column>
                    <el-table-column label="" prop="sb.email">
                    </el-table-column>

                  </el-table>
                </template>
                <br>
                <br>
                <!--  -->
                <el-pagination @size-change="handleSizeChange"
                               @current-change="handleCurrentChange"
                               :current-page="currentPage"
                               :page-sizes="[5, 10, 20, 40]"
                               :page-size="pagesize"
                               layout="total, sizes, prev, pager, next, jumper"
                               :total="studentList.length">
                </el-pagination>

              </div>
            </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="studentFormVisible=false"></el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="teacherFormVisible" :close-on-click-modal="false" v-loading="teacherLoading">
      <el-row>
        <el-col :spam="6">
          :{{teacher.name}}
        </el-col>
        <el-col :span="12">

        </el-col>
      </el-row>
      <el-row>
        <el-col :spam="12">
          :{{teacher.director}}
        </el-col>
        <el-col :span="12">

        </el-col>
      </el-row>
      <el-row>
        <el-col :spam="12">
          :{{teacher.phone}}
        </el-col>
        <el-col :span="12">

        </el-col>
      </el-row>
      <el-row>
        <el-col :spam="12">
          :{{teacher.email}}
        </el-col>
        <el-col :span="12">

        </el-col>

      </el-row>
      <el-row>
        <el-col :spam="12">
          :{{teacher.sex}}
        </el-col>
        <el-col :span="12">

        </el-col>

      </el-row>
      <el-row>
        <el-col :spam="12">
          :{{teacher.adress}}
        </el-col>
        <el-col :span="12">

        </el-col>

      </el-row>
      <div slot="footer" class="dialog-footer">
        <el-button @click="teacherFormVisible=false"></el-button>
      </div>
    </el-dialog>


  </section>
</template>

<script>
  export default {
    name: "TeaClassInfo",
    created() {
      this.initdata();
    },
    mounted() {
      this.getAllClass();
    },
    methods: {
      // 
      getAllClass() {
        this.initLoading = true;
        var _this = this;
        this.postRequest('/teacher/teaclassinfocontroller/getallclass', {
          director: _this.classForm.director,
          major: _this.classForm.major,
          department: _this.classForm.department,
          dept: _this.classForm.dept
        }).then(resp => {
          if (resp && resp.status == 200) {
            var data = resp.data;
            console.info("classlist--------->" + JSON.stringify(data));
            _this.classList = data;
            _this.initLoading = false;
          }
        });
      },
      //
      initdata() {
        var _this = this;
        this.postRequest('/teacher/teaclassinfocontroller/initdata', {}).then(resp => {
          if (resp && resp.status == 200) {
            var data = resp.data;
            console.info("arraylist--------->" + JSON.stringify(data));
            _this.majorList = data[0];
            console.info("0-------------------->" + JSON.stringify(_this.majorList));
            _this.departmentList = data[1];
            console.info("1-------------------->" + JSON.stringify(_this.departmentList));
            _this.deptList = data[2];
            console.info("2-------------------->" + JSON.stringify(_this.deptList));
            _this.directorList = data[3];
            console.info("3-------------------->" + JSON.stringify(_this.directorList));
          }
        });
      },

      indexMethod(index) {
        return index + 1;
      },

      //
      selectChange: function (val) {
        this.selectList = val;
      },

      //
      handleDeleteList: function () {
        let nowid = this.$store.state.user.id;
        const length = this.selectList.length;
        let id = "";
        for (let i = 0; i < length; i++) {
          if (nowid == this.selectList[i].id) {
            this.$message({
              message: "",
              type: "error"
            });
            return;
          }
          id += this.selectList[i].id + ",";
        }

        //,
        id = id.substring(0, id.length - 1);
        console.info(id);
        this.$confirm("?", "", {
          type: "warning"
        }).then(() => {
          this.listLoading = true;
          this.postRequest('/system/sysuser/deletelistusers', {
            id: id
          }).then(resp => {
            if (resp && resp.status == 200) {
              this.listLoading = false;
              this.selectList = [];
              this.handleUserList();
            }
          });
        })
          .catch(() => {
          });
      },

      // currentPagepagesizedata
      handleSizeChange: function (size) {
        this.pagesize = size;
        console.log(this.pagesize)  //
      },
      handleCurrentChange: function (currentPage) {
        this.currentPage = currentPage;
        console.log(this.currentPage)  //
      },

      //
      showStudentInfo(row) {
        this.studentFormVisible = true;
        this.studentLoading = true;
        var _this = this;
        console.info(JSON.stringify(row))
        this.postRequest('/teacher/teaclassinfocontroller/getclassstudent', {
          id: row.class_id
        }).then(resp => {
          if (resp && resp.status == 200) {
            _this.studentList = resp.data;
            console.info(JSON.stringify(resp.data));
            this.studentLoading = false;
          }
        });

      },
      // 
      showdirector(row) {
        if (row.director != "") {
          this.teacherFormVisible = true;
          this.teacherLoading = true;
          var _this = this;
          this.postRequest('/teacher/teaclassinfocontroller/getteacherinfo', {id: row.director}).then(resp => {
            _this.teacher = resp.data;
            _this.teacherLoading = false;
          });
        }
      },
      //
      directorchage(row) {
        this.postRequest('/teacher/teaclassinfocontroller/updatedirector', {
          tid: row.id,
          id: row.director
        }).then(resp => {

        });
      },

    },
    data() {
      return {

        initLoading: false,
        //
        //
        currentPage: 1, //
        pagesize: 10,    //    
        classList: [],
        //
        majorList: [],
        //
        departmentList: [],
        //
        deptList: [],
        //
        directorList: [],

        classForm: {
          id: "",
          class_id: "",
          name: "",
          director: "",
          grade: "",
          major_id: "",
          major: "",
          department: "",
          dept: "",
        },

        //data
        selectList: [],

        // 

        studentList: [],
        studentLoading: false,
        studentFormVisible: false,

        // 

        teacher: {},
        teacherLoading: false,
        teacherFormVisible: false,
      }
    }

  }
</script>

<style scoped>
  .container {
    padding: 30px;
    background: #fff;
    border: 1px solid #ddd;
    border-radius: 5px;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.12), 0 0 6px 0 rgba(0,0,0,.04);
  }
</style>



