<template>
  <section>
  <div class="table"> <!-- begin -->
    <div class="container"><!-- begin -->
      <div class="handle-box"> <!-- begin -->
        <div class="divbuju">
        <!---->
        <el-row :gutter="20">
          <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters" style="height: 40px">
              <el-form-item>
                
                <el-input v-model="filters.name" placeholder="" style="width:100px; heght:30px;"
                          size="mini"></el-input>
              </el-form-item>
              <el-form-item>
                
                <el-input v-model="filters.idNumber" placeholder="" style="width:100px; heght:30px;"
                          size="mini"></el-input>
              </el-form-item>

              <el-form-item>
                
                <el-select v-model="filters.sex" placeholder="" clearable size="mini">
                  <el-option v-for="item in filters.sexOptions" :key="item.value" :label="item.label"
                             :value="item.value"></el-option>
                </el-select>
              </el-form-item>

              <el-form-item>
                
                <el-select v-model="filters.major" placeholder="" clearable size="mini">
                  <el-option v-for="item in majorOptions" :key="item.name" :label="item.name"
                             :value="item.name"></el-option>
                </el-select>
              </el-form-item>

              <el-form-item>
                
                <el-select v-model="filters.state" placeholder="" clearable size="mini">
                  <el-option v-for="item in filters.stateOptions" :key="item.value" :label="item.label"
                             :value="item.value"></el-option>
                </el-select>
              </el-form-item>

              <el-form-item>
                <el-button type="primary" icon="el-icon-search" @click="handleUserList()" size="mini"></el-button>
              </el-form-item>

            </el-form>
          </el-col>

        </el-row>
      </div>

      </div> <!-- end -->
      <!--      -->
      <div class="divbuju" style="padding-top: 20px">
      <el-row>

        <el-col :span="8">
          <div class="grid-content bg-purple">
            <el-button type="success" icon="el-icon-circle-plus-outline" @click="submit($event)" size="mini" round>Excel</el-button> <input type="file"  @change="getFile($event)">
          </div>
        </el-col>
        <el-col :span="8">
          <div class="grid-content bg-purple-light">
            <el-button type="success" icon="el-icon-circle-plus-outline" @click="beforeexportExcel()" size="mini" round>Excel</el-button>
          </div>
        </el-col>
      </el-row>
      </div>


      <div class="divbuju" style="padding-top: 10px">
      <el-row>
        <el-col :span="4">
          <div class="grid-content bg-purple">
            <el-button type="success" icon="el-icon-circle-plus-outline" @click="handleAdd" size="mini" round></el-button>
            <el-button type="danger" icon="el-icon-delete" @click="handleDeleteList" size="mini" round></el-button>
          </div>
        </el-col>
        <el-col :span="20">
          <div class="grid-content bg-purple-light">
          </div>
        </el-col>
      </el-row>

      <template>
        <!---->
        <el-table :data="userList.slice((currentPage-1)*pagesize,currentPage*pagesize)"
                  size="mini"
                  highlight-current-row border
                  class="el-tb-edit mgt20"
                  ref="multipleTable"
                  tooltip-effect="dark"
                  v-loading="listLoading"
                  @selection-change="selectChange">
          <!---->
          <el-table-column type="selection" width="55">
          </el-table-column>
          <!---->
          <el-table-column type="index" >
          </el-table-column>
          <el-table-column label="" prop="id">
          </el-table-column>
          <el-table-column label="" prop="name">
          </el-table-column>
          <el-table-column label="" prop="sex">
          </el-table-column>
          <el-table-column label="" prop="birthday">
          </el-table-column>
          <el-table-column label="" prop="phone">
          </el-table-column>
          <el-table-column label="" prop="idNumber">
          </el-table-column>
          <el-table-column label="" prop="email">
          </el-table-column>
          <el-table-column label="" prop="parentPhone">
          </el-table-column>
          <el-table-column label="" prop="adress">
          </el-table-column>
          <el-table-column label="" prop="major" >
          </el-table-column>
          <el-table-column label="" prop="state" :formatter="formatClass" sortable>
          </el-table-column>
          <el-table-column fixed="right" label="" width="150">
            <template slot-scope="scope">  <!-- -->
              <el-button size="small" @click="handleEdit(scope.$index,scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </template>
      <br>
      <br>
      <!--  -->
      <el-pagination @size-change="handleSizeChange"
                     @current-change="handleCurrentChange"
                     :current-page="currentPage"
                     :page-sizes="[5, 10, 20, 40]"
                     :page-size="pagesize"
                     layout="total, sizes, prev, pager, next, jumper"
                     :total="userList.length">
      </el-pagination>

      </div>

      <div class="divbuju">
        <h1> {{numbersum}}</h1>
        <div :style="{height:'400px',width:'100%'}" id="myEchart"></div>
      </div>
    </div><!-- end-->

  </div><!-- end -->





    <!--or-->
    <el-dialog title="or" :visible.sync="addroupdatesbFormVisible" :close-on-click-modal="false">
      <el-form :model="addroupdatesb" label-width="80px"  ref="addroupdatesb">
        <el-form-item label="" prop="name">
          <el-input v-model="addroupdatesb.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="sex">
          <el-select v-model="addroupdatesb.sex" placeholder="" clearable size="mini">
            <el-option v-for="item in filters.sexOptions" :key="item.value" :label="item.label"
                       :value="item.value"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="" prop="birthday">
          <el-date-picker  v-model="addroupdatesb.birthday" type="date"  placeholder="" value-format="yyyy-MM-dd"> </el-date-picker>
        </el-form-item>

        <el-form-item label="" prop="phone">
          <el-input v-model="addroupdatesb.phone" auto-complete="off"></el-input>
        </el-form-item>

        <el-form-item label="" prop="idNumber">
          <el-input v-model="addroupdatesb.idNumber" auto-complete="off"></el-input>
        </el-form-item>

        <el-form-item label="" prop="email">
          <el-input v-model="addroupdatesb.email" auto-complete="off"></el-input>
        </el-form-item>

        <el-form-item label="" prop="parentPhone">
          <el-input v-model="addroupdatesb.parentPhone" auto-complete="off"></el-input>
        </el-form-item>

        <el-form-item label="" prop="adress">
        <el-input v-model="addroupdatesb.adress" auto-complete="off"></el-input>
      </el-form-item>

        <el-form-item label="" prop="major">

          <el-select v-model="addroupdatesb.major" placeholder="" clearable>
            <el-option v-for="item in majorOptions" :key="item.name" :label="item.name"
                       :value="item.name"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="" prop="state">

          <el-select v-model="addroupdatesb.state" placeholder="" clearable size="mini">
            <el-option v-for="item in filters.stateOptions" :key="item.value" :label="item.label"
                       :value="item.value"></el-option>
          </el-select>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addroupdatesbFormVisible=false"></el-button>
        <el-button type="primary" @click="addSubmit" :loading="addroupdatesbLoading"></el-button>
      </div>
    </el-dialog>

<!--  -->
    <el-dialog title="" :visible.sync="exportFormVisible" :close-on-click-modal="false">
      <el-input v-model="exportname" placeholder="" style="width:100px; heght:30px;"
                size="mini"></el-input>
      <div slot="footer" class="dialog-footer">
        <el-button @click="exportFormVisible=false"></el-button>
        <el-button type="primary" @click="exportExcel" ></el-button>
      </div>
    </el-dialog>


  </section>
</template>

<script>
  import echarts from "echarts";
  //   import '../../node_modules/echarts/map/js/world.js'
  import '../../assets/js/china.js' // 
  export default {
    name: "TeaStudentBasic",
    created() {
      this.selectmajors()
    },
    mounted() {
      this.handleUserList();


    },

    beforeDestroy() {
      if (!this.chart) {
        return;
      }
      this.chart.dispose();
      this.chart = null;
    },
    methods: {
      // currentPagepagesizedata
      handleSizeChange: function (size) {
        this.pagesize = size;
        console.log(this.pagesize)  //
      },
      handleCurrentChange: function (currentPage) {
        this.currentPage = currentPage;
        console.log(this.currentPage)  //
      },
      handleUserList() {
        this.listLoading=true;
        var _this = this;
        console.info(JSON.stringify(this.filters))
        this.postRequest('/teacher/teacherstudentbasic/selectallstudentbasic', {
          name: this.filters.name,
          sex: this.filters.sex,
          idNumber: this.filters.idNumber,
          major: this.filters.major,
          state: this.filters.state
        }).then(resp => {
          if (resp && resp.status == 200) {
            var data = resp.data;
            console.info(JSON.stringify(data));
            _this.userList = data;
            //
            _this.getTJ();
            _this.listLoading=false;
          }
        });
      },
      //
      selectChange: function (val) {
        this.selectList = val;
      },
      //
      selectmajors() {
        this.postRequest('/teacher/teacherstudentbasic/selectmajor', {}).then(resp => {
          if (resp && resp.status == 200) {
            var data = resp.data;
            console.info(JSON.stringify(data));
            this.majorOptions = data;
          }
        });
      },
      handleAdd(){
        this.addroupdatesbFormVisible=true;
      },
      handleEdit(index,row){
        this.addroupdatesb.update="update";
        this.addroupdatesb.id=row.id;
        this.addroupdatesb.name=row.name;
        this.addroupdatesb.sex=row.sex;
        this.addroupdatesb.birthday=row.birthday;
        this.addroupdatesb.phone=row.phone;
        this.addroupdatesb.idNumber=row.idNumber;
        this.addroupdatesb.email=row.email;
        this.addroupdatesb.parentPhone=row.parentPhone;
        this.addroupdatesb.adress=row.adress;
        this.addroupdatesb.major=row.major;
        this.addroupdatesb.state=row.state;
        this.addroupdatesbFormVisible=true;
      },
      addSubmit(){
        this.addroupdatesbLoading=true;
        this.postRequest('/teacher/teacherstudentbasic/addorupdatestudentbasic', {
          id:  this.addroupdatesb.id,
          name: this.addroupdatesb.name,
          sex: this.addroupdatesb.sex,
          birthday: this.addroupdatesb.birthday,
          phone:  this.addroupdatesb.phone,
          idNumber: this.addroupdatesb.idNumber,
          email: this.addroupdatesb.email,
          parentPhone: this.addroupdatesb.parentPhone,
          adress: this.addroupdatesb.adress,
          major: this.addroupdatesb.major,
          state: this.addroupdatesb.state,
          update: this.addroupdatesb.update,
        }).then(resp => {
          this.$refs["addroupdatesb"].resetFields();
            this.handleUserList();
        });
        this.addroupdatesbLoading=false;
        this.addroupdatesbFormVisible=false;
      },
      //
      handleDeleteList: function () {
        const length = this.selectList.length;
        let id = "";
        for (let i = 0; i < length; i++) {
          id += this.selectList[i].id + ",";
        }
        //,
        id = id.substring(0, id.length - 1);
        console.info(id);
        this.$confirm("?", "", {
          type: "warning"
        }).then(() => {
          this.postRequest('/teacher/teacherstudentbasic/deletestudentbasic', {id:id}).then(resp => {
            this.handleUserList();
          });
        })
          .catch(() => {
          });
      },

      //excel
      getFile: function (event) {
        this.file = event.target.files[0];
        console.log(this.file);
      },
      submit: function (event) {
        if(this.file == null){
          this.$message({
              message: "",
              type: "error"
            });
          return ;
        }
        //
        event.preventDefault();
        let formData = new FormData();
        formData.append("file", this.file);
        this.uploadFileRequest('/teacher/teacherstudentbasic/addstudentbasic', formData).then(resp => {
          this.handleUserList();
        });

      },
        //excel
      //excel
      beforeexportExcel(){
        this.exportFormVisible=true;
      },
      //excel
      exportExcel(){
        if(this.exportname==""){
          var myDate = new Date();
          var myYear = myDate.getYear();
          var myMonth = myDate.getMonth();
          var myHour= myDate.getHours();
          var myMinute = myDate.getMinutes();
          var mySecond = myDate.getSeconds();
          this.exportname = myYear+myMonth+myHour+myMinute+mySecond;
        }
        window.location="http://localhost:8081/teacher/teacherstudentbasic/exportdstudentbasic?exportname="+this.exportname;
        this.exportFormVisible=false;
      },


      //
      getTJ(){
        this.postRequest('/teacher/teacherstudentbasic/selectallstudentbasicTJ').then(resp => {
          this.personnumberdata=resp.data[0];
          this.numbersum=resp.data[1];
          this.chinaConfigure();
        });
      },

      chinaConfigure() {
        // console.log(this.userJson)document.getElementById('myEchart')this.$refs.myEchart
        let myChart = echarts.init(document.getElementById('myEchart')); //
        window.onresize = myChart.resize;
        var optionMap = { // 
          backgroundColor: "#02AFDB",
            tooltip: {// 
        },
          dataRange: {
            show: false,
              min: 0,
              max: 1000,
              text: ['High', 'Low'],
              realtime: true,
              calculable: true,
              color: ['orangered', 'yellow', 'lightskyblue']
          },
          geo: { // 
            map: 'china', // 
              roam: true,
              label: {
              normal: {
                show: true, // 
                  textStyle: {
                  color: 'rgba(0,0,0,0.4)'
                }
              }
            },
            itemStyle: {
              normal: {
                borderColor: 'rgba(0, 0, 0, 0.2)'
              },
              emphasis: {
                areaColor: null,
                  shadowOffsetX: 0,
                  shadowOffsetY: 0,
                  shadowBlur: 20,
                  borderWidth: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          },
          series: [{
            type: 'scatter',
            coordinateSystem: 'geo' // 
          },
            {
              name: '', // 
              type: 'map',
              geoIndex: 0,
              data: this.personnumberdata,
            }
          ]
        }

        myChart.setOption(optionMap);
      },


    },
    data() {
      return {
        currentPage: 1, //
        pagesize: 10,    //    
        userList: [],
        //
        filters: {
          name: "",
          sex: "",
          //
          sexOptions: [
            {
              value: "",
              label: ""
            },
            {
              value: "",
              label: ""
            }
          ],
          idNumber: "",
          major: "",

          state: "0",
          //
          stateOptions: [
            {
              value: "1",
              label: ""
            },
            {
              value: "0",
              label: ""
            }
          ],
        },
      //
        majorOptions: [],
        listLoading: false,
        //data
        selectList: [],

        // 
        addroupdatesb: {
          id:"",
          name:"",
          sex:"",
          birthday:"",
          phone:"",
          idNumber:"",
          email:"",
          parentPhone:"",
          adress:"",
          major:"",
          state:"",
          update:"",
        },
        addroupdatesbFormVisible:false,
        addroupdatesbLoading:false,

        file:null,
        //:formatter="formatNationality" sortable
        formatClass: function(row, column) {
          return row.state == "0"
            ? ""
            : row.state == "1" ? "" : "";
        },
        exportname:"",
        exportFormVisible:false,


        chart: null,

        //
        personnumberdata:[],

        //
        numbersum:"",
      }

    },
  }
</script>

<style scoped>
  .container {
    padding: 30px;
    background: #fff;
    border: 1px solid #ddd;
    border-radius: 5px;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.12), 0 0 6px 0 rgba(0,0,0,.04);
  }
</style>



